.timeline {
    position: relative;
    padding: 21px 0px 10px;
    margin-top: 4px;
    margin-bottom: 30px;
}

.h4 {
    font-size: 18px;
}

.timeline .line {
    position: absolute;
    width: 4px;
    display: block;
    background: currentColor;
    top: 0px;
    bottom: 0px;
    margin-left: 30px;
}

.timeline .separator {
    border-top: 1px solid currentColor;
    padding: 5px;
    padding-left: 40px;
    font-style: italic;
    font-size: .9em;
    margin-left: 30px;
}

.timeline .line::before { top: -4px; }
.timeline .line::after { bottom: -4px; }
.timeline .line::before,
.timeline .line::after {
    content: '';
    position: absolute;
    left: -4px;
    width: 12px;
    height: 12px;
    display: block;
    border-radius: 50%;
    background: currentColor;
}

.timeline .panel {
    position: relative;
    margin: 10px 0px 21px 70px;
    clear: both;
}

.timeline .panel::before {
    position: absolute;
    display: block;
    top: 8px;
    left: -24px;
    content: '';
    width: 0px;
    height: 0px;
    border: inherit;
    border-width: 12px;
    border-top-color: transparent;
    border-bottom-color: transparent;
    border-left-color: transparent;
}

.timeline .panel .panel-heading.icon * { font-size: 20px; vertical-align: middle; line-height: 40px; }
.timeline .panel .panel-heading.icon {
    position: absolute;
    left: -59px;
    display: block;
    width: 40px;
    height: 40px;
    padding: 0px;
    border-radius: 50%;
    text-align: center;
    float: left;
}

.timeline .panel-outline {
    border-color: transparent;
    background: transparent;
    box-shadow: none;
}

.timeline .panel-outline .panel-body {
    padding: 10px 0px;
}

.timeline .panel-outline .panel-heading:not(.icon),
.timeline .panel-outline .panel-footer {
    display: none;
}

.dhn-info {
    width: 100%;
    display: inline-block;
}

.dhn-info .dhn-text {
    width: 50%;
    height: auto;
    float: left;
    padding: 20px 10px 0px 35px;
}

.dhn-info .dhn-text p {
    font-family: arial;
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
    word-spacing: 5px;
    margin: 0;
    padding: 0;
    display: block;
}

.dhn-info .dhn-img {
    width: 40%;
    height: 100%;
    float: right;
}

.dhn-info .dhn-text p>span.text-purple {
    color: #9b59b6;
}

.dhn-info .dhn-img img {
    display: block;
    height: auto;
    max-width: 100%;
}

.dropdown-status {
    float: left;
    overflow: hidden;
}

.dropdown-status {
    border: none;
    outline: none;
    color: black;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
}

.status-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.status-content .status-button {
    float: none;
    color: black;
    text-decoration: none;
    text-align: left;
}

.status-content:hover {
    background-color: #ddd;
}

.dropdown-status:hover .status-content {
    display: block;
}

.assignee {
    float: right;
    font-style: italic;
}