}

.dropdown-status {
    float: left;
    overflow: hidden;
}

.dropdown-status {
    border: none;
    outline: none;
    color: black;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
}

.status-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.status-content .status-button {
    float: none;
    color: black;
    text-decoration: none;
    text-align: left;
}

.status-content:hover {
    background-color: #ddd;
}

.dropdown-status:hover .status-content {
    display: block;
}

.assignee {
    float: right;
    font-style: italic;
}
